<template>
    <v-dialog
      v-model="dialog"
      width="500"
      persistent
    >
        <v-btn @click="finish">FINISH TRAVEL</v-btn>
    </v-dialog>
  </template>
  
  <script>
  export default {
      name: 'FinishTravel',
      props:{
          value: {
              type: Boolean,
              required: true
          }
      },
      computed: {
          dialog:{
              get(){
                  return this.value
              },
              set(value){
                  this.$emit('input', value)
              }
          }
      },
      methods:{
        finish(){
            this.dialog = false,
            this.$router.push('/app')
        }
      }
  }
  </script>
  
