import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import DriverAppView from '../views/DriverAppView.vue'
import NextTravel from '@/views/NextTravel.vue'
import TravelHistoryView from '@/views/TravelHistoryView.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/app',
    name: 'app',
    component: DriverAppView
  },
  {
    path: '/next-travel',
    name: 'next-travel',
    component: NextTravel
  },
  {
    path: '/travel-history',
    name: 'travel-history',
    component: TravelHistoryView
  }
]

const router = new VueRouter({
  routes
})

export default router
