<template>
    <v-container>
        <v-card>
          <v-card-title>Historial de viajes</v-card-title>
          <v-divider></v-divider>
          <div v-if="$vuetify.breakpoint.xs">
                <v-row class="px-4 pt-3" align-self="center" justify="center">
                    <v-col cols="3" class="px-1 text-center">
                        Fecha
                    </v-col>
                    <v-col cols="3" class="px-1 text-center">
                        Destinos
                    </v-col>
                    <v-col cols="3" class="px-1 text-center">
                        Estado
                    </v-col>
                    <v-col cols="3" class="px-1 text-center">
                        Acciones
                    </v-col>
                </v-row>
                <v-divider class="mt-4"></v-divider>
              <v-row v-for="item in items" :key="item.id" class="px-4 pt-3">
                  <v-col cols="3" class="px-1 text-center">
                      {{ item.date }}
                  </v-col>
                  <v-col cols="3" class="px-1 text-center">
                      {{ item.destinations }}
                  </v-col>
                  <v-col cols="3" class="px-1 text-center">
                      {{ item.status }}
                  </v-col>
                  <v-col cols="3" class="px-1">
                    <v-btn block color="success" @click="moreInfo(item.id)">
                        <v-icon>info</v-icon>
                    </v-btn>
                  </v-col>
                  <v-divider></v-divider>
              </v-row>
          </div>
          <v-data-table
              v-else
              :headers="headers"
              :items="items"
              :header-props="headerProps"
          >
              <template v-slot:item="row">
                  <tr>
                      <td class="text-center">{{row.item.date}}</td>
                      <td class="text-center">{{row.item.destinations}}</td>
                      <td class="text-center">{{row.item.status}}</td>
                      <td class="text-center">
                          <v-btn block color="success" @click="moreInfo(row.item.id)">
                              <v-icon>info</v-icon>
                          </v-btn>
                      </td>
                  </tr>
              </template>
          </v-data-table>
        </v-card>
        <travel-details :id="travelId" v-model="travelDetails" v-if="travelDetails"/>
    </v-container>
</template>

<script>
import axios from 'axios'
import dayjs from 'dayjs'

import { TravelDetails } from '@/components/TravelDetails'

export default {
    name: 'travel-history-view',
    components: {
        TravelDetails
    },
    data(){
        return{
            travelId: '',
            travelDetails: false,
            headerProps: {
                sortByText: "Ordenar por..."
            },
            headers:[
                {
                    text: 'Fecha',
                    value: 'date',
                    align: 'center'
                },
                {
                    text: 'Destinos',
                    value: 'destinations',
                    align: 'center'
                },
                {
                    text: 'Estado',
                    value: 'status',
                    align: 'center'
                },
                {
                    text: 'Acciones',
                    value: 'actions',
                    align: 'center'
                }
            ],
            items: []
        }
    },
    async created(){
        const {data: {travels}} = await axios.post(process.env.VUE_APP_API + '/api/driver-travels', {
            token: this.$store.state.token
        })

        this.items = travels.map(travel => {
            return {
                id: travel._id,
                date: dayjs(travel.updatedAt).format('YYYY-MM-DD'),
                destinations: travel.destinations.length,
                status: travel.status
            }
        })
    },
    methods: {
        moreInfo(id){
            this.travelId = id
            setTimeout(() => {
                this.travelDetails = true
            }, 1);
        }
    }
}
</script>

