<template>
  <v-dialog v-model="dialog" fullscreen hide-overlay>
    <v-container>
        <v-card class="pa-3" height="97vh" :loading="loading">
            <v-row>
                <v-col cols="1">
                    <v-btn fab color="grey" @click="dialog = false" class="white--text">
                    <v-icon>close</v-icon></v-btn>
                </v-col>
                <v-col offset="1" cols="8">
                    <v-card-title>Detalles del viaje</v-card-title>
                </v-col>
            </v-row>
            <v-divider class="my-3"></v-divider>
            <v-row v-if="!loading">
                <v-col cols="12">
                    <v-icon left color="primary">fingerprint</v-icon> Identificación: {{ travel._id }}
                </v-col>
                <v-col cols="12">
                    <v-icon left color="primary">today</v-icon> Fecha de creación: {{ travel.createdAt }}
                </v-col>
                <v-col cols="12">
                    <v-icon left color="primary">date_range</v-icon> Tipo de entrega: {{ travel.delivery }}
                </v-col>
                <v-col cols="12">
                    <v-icon left color="primary">person_outline</v-icon> Cliente: {{ travel.designedClient }}
                </v-col>
                <v-col cols="12">
                    <v-expansion-panels>
                        <v-expansion-panel>
                            <v-expansion-panel-header>
                                <v-row>
                                    <v-col cols="12">
                                        <v-icon left color="primary">map</v-icon> Destinos
                                    </v-col>
                                </v-row>
                            </v-expansion-panel-header>
                            <v-expansion-panel-content>
                                <v-row>
                                    <v-col cols="12" v-for="destination in travel.destinations" :key="destination._id">
                                        <v-icon left color="red">place</v-icon>{{destination.address}}
                                    </v-col>
                                </v-row>
                            </v-expansion-panel-content>
                        </v-expansion-panel>
                    </v-expansion-panels>
                </v-col>
                <v-col cols="12">
                    <v-icon left color="primary">paid</v-icon> Metodo de pago: {{ travel.methodOfPay }}
                </v-col>
                <v-col cols="12">
                    <v-icon left color="primary">place</v-icon> Dirección de origen: {{ travel.origin.address }}
                </v-col>
                <v-col cols="12">
                    <v-icon left color="primary">fmd_good</v-icon> Lugar de pago: {{ travel.payLocation }}
                </v-col>
                <v-col cols="12">
                    <v-icon left color="primary">category</v-icon> Estado del viaje: {{ travel.status }}
                </v-col>
                <v-col cols="12">
                    <v-icon left color="primary">event</v-icon> Ultima actualización: {{ travel.updatedAt }}
                </v-col>
                <v-col cols="12">
                    <v-icon left color="primary">fullscreen_exit</v-icon> Tamaño del vehículo: {{ travel.vehicle }}
                </v-col>
            </v-row>
        </v-card>
    </v-container>
  </v-dialog>
</template>

<script>
import axios from 'axios'
import { mapMutations } from 'vuex'
export default {
    name: 'TravelDetails',
    props: {
        value: {
            type: Boolean,
            required: true
        },
        id: {
            type: String,
            required: true
        }
    },
    data(){
        return{
            travel: {},
            loading: true
        }
    },
    computed: {
        dialog: {
            get(){
                return this.value
            },
            set(value){
                this.$emit('input', value)
            }
        }
    },
    async created(){
        this.loading = true
        try {
            const {data: {travel}} = await axios.get(process.env.VUE_APP_API + '/api/travel/' + this.id)
            
            this.travel = travel
            this.loading = false
        } catch (error) {
            this.setErrorAlert(true)
            this.setErrorAlertText(`${error.message} - ${error.data.response.msg}`)
            this.loading = false
            this.dialog = false
        }
    },
    methods:{
        ...mapMutations(['setErrorAlert', 'setErrorAlertText'])
    }
}
</script>

