<template>
  <v-dialog
    v-model="dialog"
    width="500"
    persistent
  >
    <v-card style="overflow: hidden" class="pa-8">
        <v-row align-self="center" justify="center">
            <v-img max-width="100" src="@/assets/payment-collection.png" />
        </v-row>
        <v-row align-self="center" justify="center">
            <v-span>Antes de irte de este destino</v-span>
        </v-row>
        <v-row align-self="center" justify="center">
            <v-card-title>Recorda cobrar ${{ pay }} en efectivo</v-card-title>
        </v-row>
        <v-btn color="primary" block class="mt-8" @click="dialog = false">OK</v-btn>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
    name: 'Payout',
    props:{
        value: {
            type: Boolean,
            required: true
        },
        pay: {
            Type: Number,
            required: true,
            default: 0
        }
    },
    computed: {
        dialog:{
            get(){
                return this.value
            },
            set(value){
                this.$emit('input', value)
            }
        }
    }
}
</script>

